import React, { FC } from 'react';
import { sample, random, range, isUndefined, sortBy, compact, values, toPairs, groupBy, get, find, filter, isEqual } from 'lodash'
import { Form, Row, Col, InputNumber, Button, message } from 'antd';
import { Rule } from 'antd/lib/form';
import { linearInterpolation } from 'simple-interpolation';
import { LINEAR_INTERPOLATION_FIELDS } from '../../constants/LinearInterpolationConstant';

const { Item, useForm } = Form;

export const LinearInterpolationComponent: FC = () => {
  const [form] = useForm();

  const onFinish = () => {
    try {
      const fields = toPairs(form.getFieldsValue(LINEAR_INTERPOLATION_FIELDS));
      const groups = values(groupBy(fields, '[0][1]'));

      const points = filter(groups, (i) => get(i, '[0][1]') && get(i, '[1][1]'));
      const point = find(groups, (i) => !get(i, '[0][1]') || !get(i, '[1][1]'));
   
      const data = points.map((point) => {
        const X = find(point as unknown[], (i) => get(i, '[0]').includes('x'));
        const Y = find(point as unknown[], (i) => get(i, '[0]').includes('y'));

        const x = get(X, '[1]');
        const y = get(Y, '[1]');

        return { x, y }
      });

      const X = find(point, (i) => get(i, '[0]').includes('x'));
      const Y = find(point, (i) => get(i, '[0]').includes('y'));

      const x = get(X, '[1]');
      const y = get(Y, '[1]');

      if (isUndefined(x)) {
        const name = get(X, '[0]');
        const value = linearInterpolation(data)({ y });
        
        form.getFieldInstance(name).focus();
        form.setFieldsValue({ [name]: Math.round(value) });
      }

      if (isUndefined(y)) {
        const name = get(Y, '[0]');
        const value = linearInterpolation(data)({ x });

        form.getFieldInstance(name).focus();
        form.setFieldsValue({ [name]: Math.round(value) });
      }
    } catch (err) {
      message.error(err.message);
    }
  }

  const onRandomClick = () => {
    const fields = form.getFieldsValue(LINEAR_INTERPOLATION_FIELDS)
    const field = sample(LINEAR_INTERPOLATION_FIELDS);

    sortBy(range(6).map(() => random(1, 100))).forEach((value, i) => {
      if (isEqual(LINEAR_INTERPOLATION_FIELDS[i], field)) {
        delete fields[LINEAR_INTERPOLATION_FIELDS[i]];
      } else {
        fields[LINEAR_INTERPOLATION_FIELDS[i]] = value;
      }
    });

    form.resetFields();
    form.setFieldsValue(fields);
  }

  const onClearClick = () => {
    form.resetFields();
  }

  const rules: Rule[] = [{
    validator: async (_, value) => {
      const fields = form.getFieldsValue(LINEAR_INTERPOLATION_FIELDS);
      const isFields = compact(values(fields)).length < LINEAR_INTERPOLATION_FIELDS.length - 1;
      return value && isFields ? Promise.reject() : Promise.resolve();
    }
  }];

  return (
    <Form className="code-box-demo" name="linear-interpolation-form" form={form} onFinish={onFinish}>
      <Row gutter={[8, 8]}>
        <Col span={24}>
          Linear Interpolation Calculator Example
        </Col>
      </Row>
      <Row gutter={[8, 8]}>
        <Col span={6}>
          <Item name="x1" hasFeedback rules={rules}>
            <InputNumber placeholder="X1" type="number" />
          </Item>
        </Col>
        <Col span={6}>
          <Item name="y1" hasFeedback rules={rules}>
            <InputNumber placeholder="Y1" type="number" />
          </Item>
        </Col>
      </Row>
      <Row gutter={[8, 8]}>
        <Col span={6}>
          <Item name="x2" hasFeedback rules={rules}>
            <InputNumber placeholder="X2" type="number" />
          </Item>
        </Col>
        <Col span={6}>
          <Item name="y2" hasFeedback rules={rules}>
            <InputNumber placeholder="Y2" type="number" />
          </Item>
        </Col>
      </Row>
      <Row gutter={[8, 8]}>
        <Col span={6}>
          <Item name="x3" hasFeedback rules={rules}>
            <InputNumber placeholder="X3" type="number" />
          </Item>
        </Col>
        <Col span={6}>
          <Item name="y3" hasFeedback rules={rules}>
            <InputNumber placeholder="Y3" type="number" />
          </Item>
        </Col>
      </Row>
      <Row gutter={8}>
        <Col span={6}>
          <Button block shape="round" type="primary" htmlType="submit">
            Calculate
          </Button>
        </Col>
        <Col span={6}>
          <Button block shape="round" type="default" onClick={onClearClick}>
            Clear
          </Button>
        </Col>
        <Col span={6}>
          <Button block shape="round" type="default" onClick={onRandomClick}>
            Random
          </Button>
        </Col>
      </Row>
    </Form>
  );
}
